.form_bgc{
    background-color: #f5f5f5;
    padding: 30px;
}
@media(min-width: 767px){
    .form_bgc{
    padding: 50px 0 30px;
    }
    .form_wrapper{
        max-width: 600px;
        margin:  0 auto;
        background: #fff;
        padding: 20px 40px;
        border-radius: 8px;
    }
}

.pepper_primary_color{
    color: #eb242a;
}
.btn-pepper{
    background: #eb242a;
    color: #fff;
    padding: 10px 50px;
}
.btn-pepper:hover{
    color: #ccc;
}
.user_list_details .title{
    display: inline-block;
    width: 100px;
    font-weight: 700;
}
.user_list_details .seperator{
    padding-right: 20px;
}
@media (min-width: 767px){
    .form_modal .modal-dialog {
        max-width: 600px;
    }
    .form_modal .modal-body {
        padding: 2rem 3rem;
    }
}